import request from '../request'


// 获取列表
export function listRegion(data) {
    return request({
        url: '/api/region/listRegion',
        method: 'post',
        data
    })
}

// 新增
export function saveRegion(data) {
    return request({
        url: '/api/region/saveRegion',
        method: 'post',
        data
    })
}

// 修改
export function updateRegion(data) {
    return request({
        url: '/api/region/updateRegion',
        method: 'post',
        data
    })
}

// 删除
export function deleteRegionById(params) {
    return request({
        url: '/api/region/deleteRegionById',
        method: 'get',
        params: params
    });
}
